@import url(https://fonts.googleapis.com/css?family=Comfortaa:400,700,300);

.loader{
    width: 100%;
    height: 100vh;
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    background: #18181b;
    display: flex;
    justify-content: center;
    align-items: center;
    color:white;
    animation: loader 4s linear forwards;
    
}

@keyframes loader {
    from {
      background-color: #000000;
      color:white; /* Initial color black */
    }
    to {
        filter: blur(2px);
        color:black;
        background-color:#fff /* Initial color black */
        /* Final color white */
    }
  }

.loader svg{
    width: 100%;
}

.loading-text{
    margin:10px 24%;
    width: 100%;
    font-weight: bold;
    color: #fff;
    /* transform: translate(.4rem); */
    margin-top: 1rem;
}